import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
/*
  Generated class for the ApiProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ApiProvider {



  api2='http://50.116.17.150:3000';

  constructor(public http: HttpClient,public storage: Storage) {
   // console.log('Hello ApiProvider Provider');
  }



    verificarLogin() {
      return new Promise(resolve => {
      this.storage.get('usr_tok_by').then((value) => {
   // console.log(value);
    value ? resolve(value) : resolve(false);
    }).catch(() => resolve(false))
    });
  }

    categoriasHome() {
    return new Promise(resolve => {
      this.http.get(this.api2+'/categoriasHome').subscribe(data => {
      console.log('d');
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

      categoriasActivas() {
    return new Promise(resolve => {
      this.http.get(this.api2+'/categoriasActivas').subscribe(data => {
      console.log('d');
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }
  

     

   registrarUsuario(credenciales) {
    return new Promise(resolve => {
      this.http.post(this.api2+'/registrarUsuario', JSON.stringify(credenciales),{headers:{'Content-Type': 'application/json'}}).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }




}


